* {
    box-sizing: border-box;
  }

html{
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  background: url("https://pixabay.com/get/5ee6d0434353b114f6de877fc120357e083edbec5751734f73287b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.flexbox{
  padding-top: 10rem;
  padding-left: 15rem;
  padding-right: 3rem;
  display: flex;
}

.item {
  display: flex;
  flex-direction: column;
  border: 2rem ;
  margin:0rem 2rem;
  max-width: 20rem;
}
.founders{
  height: 20rem;
  width: 20rem;
}

.text{
  display: flex;
  
}
.textColor{
  color: white;
}

img{
  border-radius: 50%;
}

.flexboxaccounts{
    padding-top: 10rem;
    padding-left: 10rem;
    padding-right: 3rem;
    display: flex;
  }
  
  .itemaccounts{
    display: flex;
    flex-direction: column;
    border: 20rem ;
    margin:0rem 10rem;
    max-width: 30rem;
  }

.forgottenPassword:hover{
    color: red;
}  
.forgottenPassword{
    color: white;
}
.flexboxforgottenpassword{
    padding-top: 10rem;
    padding-left: 30rem;
    padding-right: 30rem;
    display: flex;
}
.passwordpage{
    color: white;
}
.flexboxproductpage{
    padding-top: 10rem;
    padding-left: 15rem;
    padding-right: 15rem;
    display: flex;
    flex-direction: column;
    color: white;
    //border: white 1px solid;

}
.flexboxproducts{
    display: flex;
    padding-left: 5rem;
    
}

.itemproducts{
  padding: 0rem 5rem;  
    align-items: center;
    //margin: 0rem 5rem;
    border: white 1px solid;
    margin: 5px;
    width: 25rem;
    //height: 20rem;
    
    
}
.title{
    text-align: center;
}
.productsbutton{
    text-decoration: none;
}

.flexboxmortgagecalculator{
    padding-top: 5rem;
    padding-left: 15rem;
    padding-right: 15rem;
    display: flex;
    flex-direction: column;
    color: white;
    //border: white 1px solid;
}

.flexboxproducts{
    display: flex;
    padding-left: 5rem;
  
}

.mortgagecalculatorbutton{
    text-align: center;
}
.flexboxmortgageform{
    display: flex;
    padding-left: 5rem;
   flex-direction: column;
}

